/* container for the content section============================================ */
.content-container{
    height:calc(100vh - 72px);
    display: flex;
    align-items: center;
    justify-content: center;
}


/* inseide the container there are 2 div  named "content1" and "content2" ======================================== */
.content1{
    height: 90%;
    width: 40%;
}
.content1 h1{
    font-size: 7vw;
    line-height: 0.9;
    font-weight: 700;
    
}
.content1 p {
text-align: justify;
width: 70%;
font-size: 1.2vw;
padding: 15px 0;
font-weight: 600;
color: rgb(92, 88, 88);
}
.button-1{
color: white;
background-color: rgb(172, 31, 31);
width: 100px;
height: 30px;
font-size: 18px;
font-weight: 500;
}
 .button-2{
color: rgb(92, 88, 88);
background-color: white;
border: 1.5px solid rgb(92, 88, 88);
width: 100px;
height: 30px;
margin-left: 30px;
font-size: 18px;
font-weight: 500;
}

.content1 h4{
    padding: 15px 0;
    font-weight: 400;
    font-size: 1.2vw;
}
.amazon{
    padding-left: 15px;
}





.content2{
    height: 90%;
    width: 40%;
}

.content2 img{
    background-size: cover;
    background-position: center;
    width: 100%;
}