
.navbar {
    height: 72px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
  }
  
  .navbar ul {
    display: flex;
    gap: 18px;
    font-weight: 500;
    font-size: 1.2vw;
  }
  
  .navbar li {
    list-style: none;
  }
  .navbar a{
    text-decoration: none;
    color: black;
  }
  
  .navbar button{
    background-color: rgb(172, 31, 31);
    color: white;
    width: 75px;
    height: 31px;
  }